  width: 100%;
  max-width: 600px;
  padding: var(--spacing-md);
  box-sizing: border-box;
}

.form-title {
  font-family: var(--typography-fontFamily-base);
  font-size: var(--typography-fontSize-xl);
  font-weight: var(--typography-fontWeight-bold);
  color: var(--neutral-foreground-1-default);
  margin-bottom: var(--spacing-md);
}

.form {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
}

.form-label {
  font-family: var(--typography-fontFamily-base);
  font-size: var(--typography-fontSize-sm);
  font-weight: var(--typography-fontWeight-medium);
  color: var(--neutral-foreground-1-default);
}

.form-required {
  color: var(--status-error-foreground-inverse-1-default);
  margin-left: var(--spacing-xs);
}

.form-input {
  padding: var(--spacing-sm);
  font-family: var(--typography-fontFamily-base);
  font-size: var(--typography-fontSize-md);
  border: 1px solid var(--neutral-stroke-1-default);
  border-radius: var(--radius-sm);
  background-color: var(--neutral-background-1-default);
  color: var(--neutral-foreground-1-default);
}

.form-input:focus {
  outline: none;
  border-color: var(--primary-stroke-1-default);
  box-shadow: 0 0 0 2px rgba(33, 150, 243, 0.1);
}

.form-input-error {
  border-color: var(--status-error-stroke-1-default);
}

.form-error {
  font-family: var(--typography-fontFamily-base);
  font-size: var(--typography-fontSize-xs);
  color: var(--status-error-foreground-inverse-1-default);
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-md);
} `, "",{"version":3,"sources":["webpack://./design-systems/system-1/components/patterns/Form/Form.css"],"names":[],"mappings":"AAAA;EACE,WAAW;EACX,gBAAgB;EAChB,0BAA0B;EAC1B,sBAAsB;AACxB;;AAEA;EACE,8CAA8C;EAC9C,wCAAwC;EACxC,8CAA8C;EAC9C,0CAA0C;EAC1C,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,sBAAsB;AACxB;;AAEA;EACE,8CAA8C;EAC9C,wCAAwC;EACxC,gDAAgD;EAChD,0CAA0C;AAC5C;;AAEA;EACE,uDAAuD;EACvD,8BAA8B;AAChC;;AAEA;EACE,0BAA0B;EAC1B,8CAA8C;EAC9C,wCAAwC;EACxC,iDAAiD;EACjD,+BAA+B;EAC/B,qDAAqD;EACrD,0CAA0C;AAC5C;;AAEA;EACE,aAAa;EACb,6CAA6C;EAC7C,6CAA6C;AAC/C;;AAEA;EACE,kDAAkD;AACpD;;AAEA;EACE,8CAA8C;EAC9C,wCAAwC;EACxC,uDAAuD;AACzD;;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,sBAAsB;EACtB,6BAA6B;AAC/B","sourcesContent":[".form-container {\r\n  width: 100%;\r\n  max-width: 600px;\r\n  padding: var(--spacing-md);\r\n  box-sizing: border-box;\r\n}\r\n\r\n.form-title {\r\n  font-family: var(--typography-fontFamily-base);\r\n  font-size: var(--typography-fontSize-xl);\r\n  font-weight: var(--typography-fontWeight-bold);\r\n  color: var(--neutral-foreground-1-default);\r\n  margin-bottom: var(--spacing-md);\r\n}\r\n\r\n.form {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: var(--spacing-md);\r\n}\r\n\r\n.form-field {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: var(--spacing-xs);\r\n}\r\n\r\n.form-label {\r\n  font-family: var(--typography-fontFamily-base);\r\n  font-size: var(--typography-fontSize-sm);\r\n  font-weight: var(--typography-fontWeight-medium);\r\n  color: var(--neutral-foreground-1-default);\r\n}\r\n\r\n.form-required {\r\n  color: var(--status-error-foreground-inverse-1-default);\r\n  margin-left: var(--spacing-xs);\r\n}\r\n\r\n.form-input {\r\n  padding: var(--spacing-sm);\r\n  font-family: var(--typography-fontFamily-base);\r\n  font-size: var(--typography-fontSize-md);\r\n  border: 1px solid var(--neutral-stroke-1-default);\r\n  border-radius: var(--radius-sm);\r\n  background-color: var(--neutral-ba